 * @import './../base/icons-heights'
 */

[data-tpl='smxp04'] .interaction-layer__point {
	width: 40px;
	height: 40px;
	border-width: 2px;
}

[data-tpl='smxp04'] .interaction-layer__point.active:before {
	transform: translate(-50%, -50%) scale(0.6);
}

[data-tpl='smxp04'] .interaction-layer__point .tooltip-trigger {
	display: block;
}

[data-tpl='smxp04'] .interaction-layer__point .tooltip-container {
	background: #ffffff;
	color: #3c3732;
	padding: 0.5rem;
	font-size: 0.9rem;
	max-width: 15rem;
	transition: visibility 0.4s 0ms ease-in-out, opacity 0.4s ease-in-out, bottom 0.4s ease-in-out, top 0.4s ease-in-out;
}

[data-tpl='smxp04'] .interaction-layer__point .tooltip-container:before {
	border-color: transparent;
	border-width: 6px;
}

[data-tpl='smxp04'] .interaction-layer__point.open .tooltip-container {
	display: block;
}

[data-tpl='smxp04'] .interaction-layer__point--1 .tooltip-container {
	bottom: auto;
	top: -20px;
	left: 95.4px;
}

[data-tpl='smxp04'] .interaction-layer__point--1 .tooltip-container:before {
	border-right-color: #ffffff;
	border-left-width: 0;
	left: -6px;
	top: 40px;
}

[data-tpl='smxp04'] .interaction-layer__point--2 .tooltip-container {
	top: 50%;
	bottom: auto;
	transform: translateY(-50%);
	left: 95.7px;
}

[data-tpl='smxp04'] .interaction-layer__point--2 .tooltip-container:before {
	border-right-color: #ffffff;
	border-left-width: 0;
	left: -6px;
	top: 50%;
	transform: translateY(-50%);
}

[data-tpl='smxp04'] .interaction-layer__point--5 .tooltip-container {
	bottom: auto;
	top: -45.7px;
	left: 95.4px;
}

[data-tpl='smxp04'] .interaction-layer__point--5 .tooltip-container:before {
	border-right-color: #ffffff;
	border-left-width: 0;
	left: -6px;
	top: 65.7px;
}

[data-tpl='smxp04'] .interaction-layer__point--3 .tooltip-container,
[data-tpl='smxp04'] .interaction-layer__point--4 .tooltip-container {
	bottom: 95.7px;
	left: 50%;
	transform: translateX(-50%);
}

[data-tpl='smxp04'] .interaction-layer__point--3 .tooltip-container:before,
[data-tpl='smxp04'] .interaction-layer__point--4 .tooltip-container:before {
	border-top-color: #ffffff;
	border-bottom-width: 0;
	left: 50%;
	bottom: -6px;
	transform: translateX(-50%);
}

[data-tpl='smxp04'] .interaction-layer__point--6 .tooltip-container {
	top: 50%;
	bottom: auto;
	transform: translate(-100%, -50%);
	left: -55.7px;
}

[data-tpl='smxp04'] .interaction-layer__point--6 .tooltip-container:before {
	border-left-color: #ffffff;
	border-right-width: 0;
	right: -6px;
	top: 50%;
	transform: translateY(-50%);
}

[data-tpl='smxp04'] .how-it-works--interactive__info-layer {
	display: none;
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	pointer-events: none;
}

[data-tpl='smxp04'] .how-it-works--interactive__info-layer a {
	pointer-events: auto;
}

[data-tpl='smxp04'] .info-layer__box--0 {
	display: none;
}

}

@media (min-width: 800px) and (max-width: 1279px) {

body.register-page [data-tpl='grid-bas-07'] .grid-bas07-content .container .row .grid-content {
	width: 50%;
	flex: 0 0 50%;
	max-width: 50%;
}

main,
footer#main-footer {
	opacity: 1;
}

}

